* {
  padding: 0;
  margin: 0; }

body {
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif; }

.my-2 {
  margin: 2rem 0; }

a.regular {
  text-decoration: none;
  color: #333; }

ul {
  list-style: none; }

#main-nav {
  display: flex;
  justify-content: space-between;
  padding: 0.5rem 2rem; }
  #main-nav ul {
    display: flex; }
  #main-nav li {
    padding: 1rem 1.5rem; }
  #main-nav a {
    text-transform: uppercase;
    border-bottom: 3px transparent solid;
    padding-bottom: 0.5rem;
    transition: color 0.5s;
    font-size: 1.5rem; }
    #main-nav a:hover {
      border-color: #ffbc00; }
    #main-nav a.current {
      text-decoration: none;
      color: #ffbc00; }

#header-home {
  background: #fff;
  height: 5rem;
  border-bottom: 3px solid #333; }

#main-footer {
  background: #333;
  color: #fff;
  height: 3rem; }
  #main-footer .footer-content {
    display: flex;
    justify-content: center;
    height: 3rem;
    align-items: center; }
    #main-footer .footer-content .social .fab {
      margin-right: 1rem;
      border: 2px #fff solid;
      border-radius: 50%;
      height: 20px;
      width: 20px;
      line-height: 20px;
      text-align: center;
      padding: 0.5rem; }
      #main-footer .footer-content .social .fab:hover {
        background: #ffbc00; }

.page.current {
  visibility: visible; }

.page.invisible {
  height: 0;
  visibility: hidden; }

#contact-c h1 {
  text-align: center;
  font-size: 2rem; }

#contact-a button[type="submit"] {
  width: 50%;
  font-size: 1.5rem; }

#contact-a .text-fields {
  display: grid;
  grid-template-areas: 'name email' 'subject phone' 'message message';
  grid-gap: 1.2rem;
  margin-bottom: 1.2rem; }
  #contact-a .text-fields .name-input {
    grid-area: name; }
  #contact-a .text-fields .subject-input {
    grid-area: subject; }
  #contact-a .text-fields .email-input {
    grid-area: email; }
  #contact-a .text-fields .phone-input {
    grid-area: phone; }
  #contact-a .text-fields .message-input {
    grid-area: message;
    height: 100px; }
  #contact-a .text-fields .text-input {
    padding: 0.5rem 1rem;
    font-size: 1.2rem; }
